<template>
  <demo title="Input" class="input-demo">
    <demo-item name="size" description="" :code="code" slot="left">
      <Input size="sm" v-model="size_test"></Input>
      <Input size="md" v-model="size_test"></Input>
      <Input size="lg" v-model="size_test"></Input>
    </demo-item>
    <demo-item name="labelFloat" description="" :code="label_code" slot="right">
      <Input size="md" label="" v-model="label_test"></Input>
      <Input size="md" placeholder="" v-model="label_test"></Input>
    </demo-item>
    <demo-item name="status" description="" :code="disabled_code" slot="left">
      <Input size="md" placeholder="" v-model="status_test"></Input>
      <Input size="md" placeholder="" :disabled="true"></Input>
    </demo-item>
    <demo-item name="color" description="" :code="color_code" slot="right">
      <Input size="md" placeholder="" v-model="color_test"></Input>
      <Input size="md" placeholder="" color="#4fae9B" v-model="color_test"></Input>
      <Input size="md" placeholder="" color="rgb(0, 160, 233)" v-model="color_test"></Input>
    </demo-item>
    <demo-item name="verify" description="" :code="verify_code" slot="left">
      <Input size="md" placeholder=""></Input>
      <Input size="md" placeholder="(7~14)" verify="phone" @verify="verify_result = arguments[0]" v-model="verify_test"></Input>
      <p class="verify-result">: {{ verify_result }}</p>
    </demo-item>
    <demo-item name="textarea" description="" :code="textarea_code" slot="right">
      <Input size="md" v-model="textarea_test"></Input>
      <Input size="md" v-model="textarea_test" placeholder="3" type="textarea"></Input>
      <Input size="md" v-model="textarea_test" type="textarea" placeholder="" :autosize="true"></Input>
    </demo-item>
  </demo>
</template>

<style lang="stylus" scoped>
  @import '../../style/';
  .hiui-input{ margin-bottom:5px }
  .verify-result { margin-top:5px;margin-bottom:5px }
</style>

<script>
  export default {
    data () {
      return {
        size_test: '2121312312',
        label_test: '',
        status_test: '',
        verify_test: '',
        color_test: '',
        textarea_test: '',
        verify_result: false,
        code: `
<Input size="sm" v-model="test"></Input>
<Input size="md" v-model="test"></Input>
<Input size="lg" v-model="test"></Input>
        `,
        label_code: `
// label
<Input size="md" label="" v-model="label_test"></Input>
<Input size="md" placeholder="" v-model="label_test"></Input>
        `,
        color_code: `
<Input size="md" placeholder="" v-model="status_test"></Input>
<Input size="md" placeholder="" color="#4fae9B" v-model="color_test"></Input>
<Input size="md" placeholder="" color="rgb(0, 160, 233)" v-model="color_test"></Input>
        `,
        disabled_code: `
<Input size="md" placeholder="" v-model="status_test"></Input>
<Input size="md" placeholder="" :disabled="true" v-model="status_test"></Input>
        `,
        verify_code: `
// API
<Input size="md" placeholder=""></Input>
<Input size="md" placeholder="(7~14)" verify="phone" @verify="verify_result = arguments[0]" v-model="verify_test"></Input>
<p class="verify-result">: {{ verify_result }}</p>
        `,
        textarea_code: `
<Input size="md" v-model="textarea_test"></Input>
<Input size="md" v-model="textarea_test" placeholder="3" type="textarea"></Input>
<Input size="md" v-model="textarea_test" type="textarea" placeholder="" :autosize="true"></Input>
        `
      }
    }
  }
</script>



