<template>
  <demo title="Radio" class="radio-demo">
    <demo-item name="size" description="" :code="code" slot="left">
      <Radio v-model="test" size="sm"></Radio>
      <Radio v-model="test" size="md">2</Radio>
      <Radio v-model="test" size="lg">3</Radio>
      <p class="radio-result">{{ test }}</p>
    </demo-item>
    <demo-item name="value" description="" :code="value_code" slot="right">
      <Radio :value="1" v-model="value_test"></Radio>
      <Radio :value="2" v-model="value_test">2</Radio>
      <Radio :value="3" v-model="value_test">3</Radio>
      <p class="radio-result">{{ value_test }}</p>
    </demo-item>
    <demo-item name="status" description="" :code="status_code" slot="left">
      <Radio v-model="status_test"></Radio>
      <Radio :disabled="true" v-model="status_test"></Radio>
      <p class="radio-result">{{ status_test }}</p>
    </demo-item>
    <demo-item name="color" description="" :code="color_code" slot="right">
      <Radio v-model="color_test"></Radio>
      <Radio color="rgb(0, 160, 233)" v-model="color_test">2()</Radio>
      <p class="radio-result">{{ color_test }}</p>
    </demo-item>
  </demo>
</template>

<style lang="stylus" scoped>
  @import '../../style/';
  .hiui-radio { margin-right:5px }
  .radio-result { margin-bottom:0;margin-top:10px }
</style>

<script>
  export default {
    data () {
      return {
        test: '',
        value_test: '',
        status_test: '',
        color_test: '',
        code: `
<Radio v-model="test" size="sm"></Radio>
<Radio v-model="test" size="md">2</Radio>
<Radio v-model="test" size="lg">3</Radio>
<p class="radio-result">{{ test }}</p>
        `,
        value_code: `
<Radio :value="1" v-model="value_test"></Radio>
<Radio :value="2" v-model="value_test">2</Radio>
<Radio :value="3" v-model="value_test">3</Radio>
<p class="radio-result">{{ value_test }}</p>
        `,
        status_code: `
<Radio v-model="status_test"></Radio>
<Radio :disabled="true" v-model="status_test"></Radio>
<p class="radio-result">{{ status_test }}</p>
        `,
        color_code: `
<Radio v-model="color_test"></Radio>
<Radio color="rgb(0, 160, 233)" v-model="color_test">2()</Radio>
<p class="radio-result">{{ color_test }}</p>
        `
      }
    }
  }
</script>



